@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  display: grid;
  height: 100vh;
  grid-template-areas:
  "header"
  "navbarAndProfile";

  grid-template-rows: 60px 1fr;
  background-color: #f1f1f1;
}


.header{
  grid-area: header;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 60px;
  background-color: #2F4F4F;
}
.working-header {
  width: 760px;
  background-color: #2F4F4F;
  margin: auto;
}


.navbarAndProfile{
  grid-area: navbarAndProfile;
  display: grid;

  grid-template-areas:
  "navbar profile";

  grid-template-columns: 110px 650px;
  position: relative;
  margin: auto;
  margin-top: 15px;
}


.navbar{
  grid-area: navbar;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
}

.profile{
  grid-area: profile;
  border-left: 1px solid #D3D3D3;
}
